/* defaults */
@keyframes changecolors {
  0% {
    color: var(--yellow);
  }
  20% {
    color: var(--red);
    translate-x: -20px;
  }
  40% {
    color: var(--green);
  }
  60% {
    color: var(--blue);
  }
  80% {
    color: var(--orange);
  }
  100% {
    color: var(--yellow);
  }
}
