:root {
  --green: #00bc87;
  --light-green: #bdffe9;
  --blue: #00c2ee;
  --yellow: #ffa800;
  --light-yellow: #ffe7bd;
  --red: #d00090;
  --light-red: #ffbdee;
  --orange: #e74c3c;
  --light-orange: #ffc0bd;
  --dark: #000000;

  --dark-blue: #063d8c;
  --light-color: #b3bfce;

  --light: #f3f3f4;
  --light-0: #fafafa;
  --light-1: #d5e0e2;
  --light-2: #777777;
  --light-3: #585858;
}

html,
body {
  background-color: rgb(255, 255, 255);
  font-family: "Inter";
}

@media (min-width: 900px) {
  .mobile {
    display: none;
  }

  body::-webkit-scrollbar {
    width: 0.8em;
  }

  body::-webkit-scrollbar-track {
    background: var(--light-0);
    padding-right: 10px;
  }

  body::-webkit-scrollbar-thumb {
    background-color: var(--dark);
    border-radius: 50px;
    border: solid 2px var(--light-0);
  }

  body::-webkit-scrollbar-corner {
    background-color: transparent;
  }
}

.header {
  padding: 20px;
  button {
    border: none;
    background: none;
    margin-left: 13px;
    font-size: 15px;
  }
}

.carousel {
  margin-top: 30px;
}

.close-btn {
  border: none;
  border-radius: 50px;
  position: absolute;
  right: 20px;
  height: 40px;
  width: 40px;
}

.cert-container {
  height: clamp(550px, 90vh, 90vh);
  display: relative;
  .title {
    font-size: 30px;
    font-family: "Grifter";
    font-weight: 700;
    line-height: 30px;
    text-transform: capitalize;
    max-width: 600px;
    letter-spacing: 1px;
    margin-bottom: 20px;
    color: var(--dark);
  }
  p {
    font-family: "Inter";
    font-size: 13px;
    font-weight: 400;
    color: var(--dark);
    line-height: 20px;
  }
}

.modal-body::-webkit-scrollbar {
  width: 0.7em;
}

.modal-body::-webkit-scrollbar-track {
  background: var(--light-0);
  padding-right: 10px;
}

.modal-body::-webkit-scrollbar-thumb {
  background-color: var(--light-2);
  border-radius: 50px;
  border: solid 2px var(--light-0);
  &:hover {
    background-color: var(--dark-blue);
  }
  &:active {
    cursor: grabbing;
  }
}

.modal-body::-webkit-scrollbar-corner {
  background-color: transparent;
}

.cert {
  margin-bottom: 30px;
  border-radius: 10px;
  border: rgb(0, 0, 0) solid 5px;
}

button {
  &:focus {
    outline: none;
  }
}
a {
  text-decoration: none;
  color: inherit;
  &:hover {
    text-decoration: none;
    color: var(--light-2);
  }
}
.ver {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.hor {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.text-dim {
  color: var(--light-2);
  transition: 0.3s;
  &:hover {
    color: var(--light);
  }
}

.gradient {
  background: linear-gradient(
    to right,
    var(--yellow),
    var(--red),
    var(--green),
    var(--blue),
    var(--orange),
    var(--yellow)
  );
  background-size: 200% auto;

  color: #000;
  background-clip: text;
  text-fill-color: transparent;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;

  animation: shine 3s linear infinite;
}

@keyframes shine {
  to {
    background-position: -200% center;
  }
}

@keyframes rad-anime {
  to {
    background-position: -200% center;
  }
}

.nav-btn {
  height: 40px;
  padding-left: 20px;
  padding-right: 20px;
  border: none;
  border-radius: 50px;
  font-weight: 700;
  margin-right: 20px;
  background: none;
  font-size: 15px;
  transition: 0.3s;
  &:hover {
    background: var(--light);
  }

  &.active {
    background: var(--light-blue);
    color: var(--blue);
  }
}
.my-badge {
  font-weight: 500;
  font-size: 16px;
  font-family: "Poppins";
  color: var(--light-3);
}
.hero {
  background: radial-gradient(
    circle at bottom left,
    #d000924b,
    transparent,
    transparent,
    transparent
  );
  margin-top: -20px;
  animation: rad-anime infinite 2s;
}

.main {
  h6 {
    margin-top: 120px;
    margin-bottom: 15px;
    font-weight: 700;
    font-size: 20px;
    font-family: "Grifter";
    color: var(--light-3);
  }
  h1 {
    font-size: 55px;
    font-family: "Grifter";
    margin-top: 0px;
    margin-bottom: 15px;
    color: var(--dark);
    text-transform: capitalize;
    line-height: 60px;
  }
