}
.call-to-action {
  display: flex;
  justify-content: space-between;
  width: 400px;
  button {
    border: none;
    background: var(--light);
    font-size: 16px;
    font-family: "Poppins";
    font-weight: 700;
    border-radius: 10px;
    transition: 0.1s;
    &:hover {
      background: var(--light);
      color: var(--dark);
    }
  }
  .hire {
    height: 48px;
    padding-left: 40px;
    padding-right: 40px;
    color: var(--dark);
    background: transparent;
    width: 100%;
    border: solid 1px var(--dark);
    font-family: "Inter";
    font-weight: 700;
    &:hover {
