    height: 50px;
    width: 50px;
    font-size: 22px;
    background: transparent;
  }
}
.square {
  border: none;
  background: var(--light);
  font-size: 16px;
  font-family: "Poppins";
  font-weight: 700;
  border-radius: 10px;
  transition: 0.3s;
  height: 50px;
  width: 50px;
  font-size: 20px;
}

.about {
